<template>
  <div id="loading" :class="{ fadeout: !isLoading }">
    <!-- <div class="loader"></div> -->
    <div class="svg-container">
      <img-base img-name="chair" viewBox="0 0 330 340">
        <img-chair />
      </img-base>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import ImgChair from "../illustrations/ImgChair-loader";
import ImgBase from "../ImgBase";
export default {
  name: "LoadingScreen",
  props: ["isLoading"],
  data(){
    return {
      isFinished: false
    }
  },
  components: {
    ImgBase,
    ImgChair,
  },
  methods: {
    drawLines_loader() {
      $(".nodules-loader").addClass("start-loader");
      $(".main-path-loader").addClass("start-loader");
      $(".main-path-loader-fill").addClass("start-loader");
      this.isFinished = true;
    },
    deleteLines_loader() {
      $(".nodules-loader").removeClass("start-loader");
      $(".main-path-loader").removeClass("start-loader");
      $(".main-path-loader-fill").removeClass("start-loader");
    },
  },
  mounted(){
    var vm = this;
    vm.drawLines_loader();

    if(vm.isFinished == true) {
      setTimeout(() => {
        vm.deleteLines_loader();
      }, 3000)
    }
  }
};
</script>

<style lang="scss" scoped>
#loading {
  background-color: #fff;
  font-size: 32px;
  height: 100vh;
  width: 100vw;
  z-index: 99999999999999999999;
  position: absolute;
  opacity: 1;
  visibility: visible;
  transition: 1.5s;

  &.fadeout {
    // animation: fadeout 2s forwards;
    opacity: 0;
    visibility: hidden;
  }
}

.svg-container {
    width: 20%;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
}

// .loader {
//   position: absolute;
//   border: 6px solid #cbcbcb;
//   border-top: 6px solid #3498db;
//   border-radius: 50%;
//   width: 30px;
//   height: 30px;
//   animation: spin 1s linear infinite;
//   left: calc(50% - 15px);
//   top: calc(50% - 15px);
// }

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes fadeout {
  to {
    opacity: 0;
    visibility: hidden;
  }
}
</style>


