<template>
  <svg
    xmlns="http://www.w3.org/2000/svg"
    :aria-labelledby="imgName"
    preserveAspectRatio="xMidYMid meet"
  >
    <title :id="imgName" lang="en">{{imgName}} animation</title>
    <g :fill="iconColor">
      <slot />
    </g>
  </svg>
</template>

<script>
/* eslint-disable */
export default {
  props: {
    imgName: {
      type: String,
      default: "chair",
    },
    iconColor: {
      type: String,
      default: "#1e1e1e",
    },
  },
};
</script>



